
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#home {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
}
footer {
  position: fixed;
  bottom: 0;
  height: 40px;
  background-color: black;
  width: 100%;
  color: grey;
  padding: 8px;
  text-align: center;
}
footer a {
    color: white;
    text-decoration: none;
}

.filterbar-container {
  color: white;
  width: 100%;
  background-color: black;
}
.filterbar-container h4 {
    display: inline-block;
    margin: 0 40px 0 20px;
}
.filterbar-container button {
    background-color: transparent;
    color: white;
    border: none;
    display: inline-block;
    margin: 0 10px;
    cursor: pointer;
}
.filterbar-container a {
    color: white;
    position: absolute;
    right: 20px;
    text-decoration: none;
}
.filterbar-container .selected {
    color: skyblue;
}

.viewer {
  height: calc(100vh - 62px);
  opacity: 1;
  position: relative;
  background-color: white;
}
.viewer button {
    position: absolute;
    bottom: 10px;
    right: 50px;
    background-color: transparent;
    height: 60px;
    width: 120px;
    border: none;
    font-size: 14px;
    cursor: pointer;
    -webkit-transition: color 0.8s;
    transition: color 0.8s;
}
.viewer button:hover {
      color: grey;
}
.words {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 0 40px;
  min-width: 70%;
}
.words h2 {
    font-size: 28px;
}
.words h4 {
    text-align: right;
    margin: 40px 0 0 0;
}
.fader {
  -webkit-animation: fader 1.2s linear 0s 1;
          animation: fader 1.2s linear 0s 1;
}
@-webkit-keyframes fader {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes fader {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
}
.allquotes-container a {
  color: white;
  text-decoration: none;
}
.allquotes-container h2 {
  text-align: center;
}
.allquotes-container h4 {
  width: 80%;
  margin: 0 auto 25px;
  font-size: 24px;
}
.allquotes-container p {
  width: 80%;
  margin: 0 auto 25px;
}
.allquotes-container .searchbar {
  background-color: black;
  padding: 5px;
  margin-bottom: 40px;
  position: relative;
}
.allquotes-container form {
  display: inline-block;
  position: absolute;
  right: 0;
}
.allquotes-container form input {
    width: 200px;
    margin-right: 10px;
}
.allquotes-container form button {
    background-color: transparent;
    color: white;
    height: 20px;
    width: 100px;
    border-radius: 5px;
}
